//container
.container {
    max-width: 1200px;
    width: 98%;
    margin: 0 auto;
    // background-color: rgba(255,255,255,0.3);
}
//layout
#main {
    background-color: var(--black);
}
//cont__title
.cont__title {
    padding: 15vmax 1% 10vmax;
    font-size: 5vmax;
    font-family: var(--main_font);
    text-transform: uppercase;
    line-height: 0.85;
    color: var(--white);

    em {
        display: block;
    }
}
// 라이트 버전
.light.cont__title {
    background-color: var(--light_bg);
    color: var(--black);
}

@media (max-width: 600px){
    .cont__title {
        text-align: center;
        padding-bottom: 10vw;
    }
}
//cont__contact
.cont__contact {
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-transform: uppercase;
    color: var(--white);
    p {
        font-size: 1.2rem;
        font-family: var(--sub_font); 
        margin-bottom: 4vw;       
    }
    h3 {
        font-family: var(--main_font);
        font-size: 9vw;
        text-align: center;
        line-height: 1;
        margin-bottom: 1vw;
        em {
            display: block;
        }
    }
    a {
        font-size: 1rem;
        font-family: var(--sub_font);         
        border: 1px solid var(--white);
        border-radius: 50px;
        padding: 1vw 4vw;
    }
}
// 타이틀 애니메이션
.cont__title {
    strong {
        display: block;
        opacity: 0;
        transform:  translateY(50px);
    }
    em {
        display:block;
        opacity: 0;
        transform:  translateY(50px);
    }
    
}